export type AppStatus =
  | 'draft'
  | 'pendingReview'
  | 'underReview'
  | 'changesRequested'
  | 'approved'
  | 'rejected'
  | 'suspended'
  | 'withdrawn';

export type AppState = 'inactive' | 'active' | 'suspended' | 'deleted';

export interface AppDefinition {
  id: string; // GUID
  developerId: string;
  developerName: string;
  name: string;
  description: string;
  shortDescription: string;
  iconUrl: string;
  status: AppStatus;
  state: AppState;

  // Integration Configuration
  integrationPoints: IntegrationPoint[];
  healthCheckUrl?: string;

  categories: string[];
  tags: string[];

  // Stats (Computed/Cached)
  averageRating: number; // 0-5
  reviewCount: number;
  installCount: number;
  isInstalled: boolean; // true if the queried owner has this app installed
  isEnabled: boolean; // true if the queried owner has this app enabled
  createdDate: string; // ISO Date
  updatedDate: string; // ISO Date
}

export interface IntegrationPoint {
  screenId: 'tripDetails' | 'positionActivityStats';
  requiredPermissions: string[]; // e.g., ['READ_VEHICLE', 'READ_TRIPS']
  baseUrl: string;
}

export interface AppReview {
  id: string;
  appId: string;
  userId: string;
  userName: string;
  organizationName: string;
  rating: number; // 1-5
  title: string;
  comment: string;
  developerResponse?: string; // Optional reply from dev
  isVerifiedPurchase: boolean;

  // Community Feedback
  helpfulCount: number; // Number of users who clicked "Was this helpful?"

  createdDate: string;
}

export const AVAILABLE_SCREENS = [
  { id: 'tripDetails', name: 'Trip Details Screen' },
  { id: 'positionActivityStats', name: 'Vehicle Activity Screen' },
];
